import google from 'google';

const mapOptions = () => ({
    draggable: true,
    streetViewControl: true,
    mapTypeControlOptions: {
        mapTypeIds: [
            google.maps.MapTypeId.SATELLITE,
            google.maps.MapTypeId.ROADMAP
        ],
    },
    mapTypeId: google.maps.MapTypeId.SATELLITE
});

const lat = (el) => parseFloat(el.dataset.lat) || null;
const lng = (el) => parseFloat(el.dataset.lng) || null;
const zoom = (el) => parseInt(el.dataset.zoom, 10) || 9;
const center = (el) => ({
    center: {
        lat: lat(el),
        lng: lng(el)
    }
});

const latLng = (lat, lng) =>
    new google.maps.LatLng(lat, lng);

const addMarker = (map, lat, lng) => {
    google.maps.event.addListenerOnce(map, 'idle', () => {

        const marker = new google.maps.Marker({ position: latLng, map: map });

        return marker;
    });
};

const render = (mapElement) => {
    
    const lat = lat(mapElement);
    const lng = lng(mapElement);

    const zoom = zoom(mapElement);
    const center = center(mapElement).center;

    const map = new google.maps.Map(mapElement, mapOptions());
    map.setCenter(center);
    map.setZoom(zoom);
    
    addMarker(map, lat, lng);
};

export { render };



